.wrapper{
  padding-top: 3rem;
  padding-left: 5rem;
  padding-right: 5rem;
  background-color:RGB(23,22,28);

}

header{
 color: white;
 
  display: flex;
  justify-content:space-between;
  align-items: center;
  flex-wrap: wrap;
  
 
}

header .name{
  font-size: 36px;
}

.links ul{
  margin: 0px;
 
  display: flex;
  
}


.links ul li{

  
  list-style: none;
  font-size: 16px;
margin: auto 28px;
}

.links ul li a{
  color: white;
  text-decoration: none;
}

.line{
  border: 1px solid white;
 
  margin-top: 2rem;
}


.imgText{
  margin-top: 8rem;
  
  display: flex;
  justify-content: space-between;
  
}




.img{
  text-align: center;
  width: 40%;
  
}

.img img{

}


.text{
  color: white;
  width: 50%;
  
}

.text .text1{
font-weight: 300;
}

.text .name {
font-size: 5rem;

margin: 5px auto auto auto;
color: RGB(254,128,93);
}


.img{
  position: relative;
}

.img img{
  position: absolute;
  left: 135px;
}

.wrapper1{
  width: 100%;
  height: 1000px;
 
background-size:cover;
  background-image: url('./download.png');
  background-position: center;
}

.wrapper2{
  width: 100%;
  height: 1000px;
  
  background-color: rgba(32, 31, 31, 0.87);
  border: 1px solid rgba(145, 57, 57, 0);
}

.release{
  position: relative;
padding: 2rem;
  width: 900px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 12rem;
  margin-bottom: 12rem;
  background-color: rgba(0, 0, 0, 0.521);
  display: flex;
 
}

.release .text{
 
width: 50%;
padding-top: 5rem;
padding-right: 5rem;

}



.release .text h2{
  margin-bottom: 2rem;
}

.release .text p{
  margin-bottom: 4rem;
  margin-right: 8rem;
}

.release .img1{
 
  width: 50%;

}

.release .img1 img{
 
  width: 100%;
}

.btns{
  margin-top: 125px;
  
 
  display: flex;
  justify-content: space-around;
}

.btn{
  
  cursor: pointer;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid RGB(254,128,93);
  color: RGB(254,128,93);
  padding: 5px;
  padding-bottom: 8px;
  padding-left: 20px;
  padding-right: 20px;
}

.btn:hover{
  background-color: RGB(254,128,93);
  color: black;
}

.ribbon {
  width: 200px;
  height: 39px;
  background:RGB(254,128,93);
  clip-path: polygon(0% 0%, 100% 0%, calc(100% - 20px) 50%, 100% 100%, 0% 100%);



  position: absolute;
  text-align: center;
  left: 0px;
  top: 77px;
  padding: 5px;
  color: black;
  font-size: 24px;
  
}

.wrapper3{
 
  background-color: RGB(31,34,47);
  
}

.wrapper3 h1{
 
  text-align: center;

}

.inWrapper3{
 

}

.inWrapper3 .row{
display: flex;
justify-content: space-between;
width: 900px;
margin: auto auto ;
}

.inWrapper3 .row .col:nth-child(3){
  position: relative;
}

.inWrapper3 .row .col{
  width: 30%;
 
  text-align: center;
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-left: 16px;
  padding-right: 16px;

}

.inWrapper3 .row .col h2,.p2,.heavy{
color: RGB(251,135,104);
}

.inWrapper3 .row .col .p1{
  color: white;
}

.inWrapper3 .row .col .p2{
  display: inline-block;
  width:150px ;
}

.inWrapper3 .row .col .line{
  border: 1px solid white;
  display: inline-block;
  width:150px ;
}

.inWrapper3 .row .col:nth-child(odd){
  padding-top: 7rem;
}

.inWrapper3 .row .col .heavy{
  text-align: center;
  font-size: 6rem;
}


.bookmarkRibbon {
  width: 40px;
  height: 60px;
  background:RGB(251,135,104);
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 50% calc(100% - 20px), 0% 100%, 0% 0%);

  position: absolute;
top: 0px;
right: 24px;

}


.wrapper4{
  background-color: RGB(23,22,28);
  border: 1px solid rgba(255, 0, 0, 0);
  text-align: center;

}


.wrapper4 p{
  color: white;
  width: 350px;
 
  margin: auto auto;
}

.wrapper4 h1{
  color:RGB(251,135,104);
}

.wrapper4 .inWrapper4 .row h4{
color: white;
}



.wrapper4 .inWrapper4{

  width: 900px;
  margin: auto auto;

}

.wrapper4 .inWrapper4 .row{
border: 1px solid white;
margin-top: 2rem;
margin-bottom: 2rem;
padding: 3rem;
}


.wrapper4 .inWrapper4 .row h4{
  width: 400px;
  margin: auto auto;
  margin-bottom: 3rem;
}

.wrapper4 .inWrapper4 .row .btn{
  padding-left: 3rem;
  padding-right: 3rem;
}


.wrapper5{
  background-color: RGB(31,34,47);
  margin: 3rem;
  margin-top: 8rem;
  margin-bottom: 8rem;
  padding-top: 10rem;
  padding-bottom: 10rem;
position: relative;

}

.wrapper5 .inwrapper5{
 
  width: 500px;
  padding: 1px;
  margin: auto auto 3rem auto;
  position: relative;
}

.wrapper5 .btn{
  padding-left: 2rem;
  padding-right: 2rem;
}

.wrapper5 img{
  position: absolute;
  top: -140%;
  left: 32%;
  border-radius: 50%;
  width: 150px;

}

footer{
  text-align: center;
  background-color: RGB(254,128,93);
  padding: 8px;
  color: black;
}

footer a{
  color: black;
}


.socSet{

width: 25px;
position: fixed;
top: 50%;
left: 1rem;
}

.socSet img{
width: 24px;
height: 24px;
display: block;
margin-top: 1rem;
margin-bottom: 1rem;
background-color: aliceblue;
}
